.home-container {
  display: flex;                     
  height: calc(100vh - 6rem);       /* subtract header + nav height */
  background: var(--dark-blue);
  justify-content: center;          
  align-items: center;              
  gap: 2rem;                         /* space between cards */
}

.card {
  background: var(--beige-light);
  border: 4px solid var(--beige-dark);
  border-radius: 8px;
  width: 280px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  padding: 1rem;
}

.card:hover {
  transform: translateY(-5px);      /* subtle lift effect */
}

.card img {
  width: 100%;
  border-radius: 4px;
}

.caption {
  display: block;
  margin-top: 0.5rem;
  font-size: 1.1rem;
  text-align: center;
  color: var(--dark-blue);
}
